h1 {
  color: red;
}

.hero {
  position: relative;
  padding-top: 10px;
  height: 100vh;
  background: -webkit-gradient(linear, left top, left bottom, from(#3F516E), color-stop(90%, transparent)), -webkit-gradient(linear, left bottom, left top, from(#000), to(transparent)), #000 no-repeat center fixed;
  background: -webkit-linear-gradient(#3F516E, transparent 90%), -webkit-linear-gradient(bottom, #000, transparent), #000 no-repeat center fixed;
  background: -o-linear-gradient(#3F516E, transparent 90%), -o-linear-gradient(bottom, #000, transparent), #000 no-repeat center fixed;
  background: linear-gradient(#3F516E, transparent 90%), linear-gradient(0deg, #000, transparent), #000 no-repeat center fixed;
  background-size: cover;
  margin-bottom: 30px;
}
